import React from 'react'
import { Query, Mutation } from 'react-apollo'
import gql from 'graphql-tag'
import styled from 'styled-components'

const OPEN_MODAL_QUERY = gql`
  query {
    openModal @client
  }
`
const TOGGLE_MODAL_MUTATION = gql`
  mutation {
    toggleModal @client
  }
`
const BackDrop = styled.div`
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
`

const Modall = styled.div`
  background-color: #fff;
  border-radius: 25px;
  /* flex-flow: column; */
  display: grid;
  grid-template-rows: 50px 1fr 80px;
  grid-template-columns: 1fr 1fr;
  position: absolute;
  align-items: center;
  width: 500px;
  height: 500px;
  padding: 10px 25px;
  z-index: 900;
  box-shadow: 1px 1px 5px 3px rgba(0, 0, 0, 0.3);
`

const Cancel = styled.button`
  background-color: #fff;
  border-radius: 5px;
  display: grid;
  grid-row: 3;
  grid-column: 2;
  position: relative;
  align-items: center;
  justify-self: flex-end;
  height: 40%;
  background: rgba(220, 100, 120, 1);
  color: white;
  width: 120px;
  font-size: 18px;
  z-index: 900;
  &:focus {
    outline: none;
  }
`
const Save = styled.button`
  background-color: #fff;
  border-radius: 5px;
  display: grid;
  grid-column: 2;
  grid-row: 3;
  margin: 0 auto;
  position: relative;
  align-items: center;
  width: 120px;
  height: 40%;
  background: rgba(20, 200, 120, 1);
  color: white;
  font-size: 18px;
  z-index: 900;
  &:focus {
    outline: none;
  }
`
const Modal = props => {
  return (
    <Mutation mutation={TOGGLE_MODAL_MUTATION}>
      {toggleModal => (
        <Query query={OPEN_MODAL_QUERY}>
          {({ data }) => {
            if (!data.openModal) {
              return null
            }
            return (
              <BackDrop>
                <Modall>
                  <p>{props.value}</p>
                  <div />
                  <Cancel onClick={toggleModal}>Cancel</Cancel>
                  <Save onClick={toggleModal}>Save</Save>
                </Modall>
              </BackDrop>
            )
          }}
        </Query>
      )}
    </Mutation>
  )
}

export default Modal
export { OPEN_MODAL_QUERY, TOGGLE_MODAL_MUTATION }
